import React, { Component } from 'react';
export default class Header extends Component {
	  constructor(props){
		  super(props);
		  this.state = {
			  active: "Home"
		  }
		  this.setActiveNav = this.setActiveNav.bind(this);
	  }
	
	  setActiveNav(newNav){
		  this.setState({active: newNav});
	  }
	
	  render() {
		let resumeData = this.props.resumeData;
		let setActiveNav = this.setActiveNav;
		return (
		<React.Fragment>
		  <header id="home">
			<nav id="nav-wrap">
			  <a className="mobile-btn" href="#nav-wrap" title="Show navigation">Show navigation</a>
			  <a className="mobile-btn" href="#" title="Hide navigation">Hide navigation</a>
			  <ul id="nav" className="nav">
				{resumeData.navigation.map((name,index) => (
					<NavItem 
						name={name}
						key={index}
						active={this.state.active}
						setActiveNav={setActiveNav}
					/>
				))}
			  </ul> {/* end #nav */}
			</nav> {/* end #nav-wrap */}
			<div className="row banner">
			  <div className="banner-text">
				<h1 className="responsive-headline">Hi, I'm {resumeData.name}.</h1>
				<h3>I'm a {resumeData.role}. Let's <a className="smoothscroll" href="#about">start scrolling </a>
				  and learn more <a className="smoothscroll" href="#about">about me</a>.</h3>
				<hr />
				<ul className="social">
				  <li><a href="http://linkedin.com/in/jamiemcdonnell" target="_blank" rel="noopener noreferrer" title="Visit my linkedIn Profile"><i className="fa fa-linkedin" /></a></li>
				  <li><a href="https://github.com/JamieMcDonnell" target="_blank" rel="noopener noreferrer" title="Visit my GitHub Profile"><i className="fa fa-github" /></a></li>
				  <li><a href="https://www.upwork.com/o/profiles/users/_~016bafa6da0ac3a402/" target="_blank" rel="noopener noreferrer" title="Visit my Upwork Profile"><i className="fa fa-globe" /></a></li>
				</ul>
			  </div>
			</div>
			<p className="scrolldown">
			  <a className="smoothscroll" href="#about"><i className="icon-down-circle" /></a>
			</p>
		  </header> {/* Header End */}
			</React.Fragment>
		);
	  }
}
	
class NavItem extends Component{
	constructor(props) {
		super(props);
		this.state = {
			active: false
		};

		// This binding is necessary to make `this` work in the callback
		this.handleClick = this.handleClick.bind(this);
	}

	handleClick(e) {
		e.preventDefault();
		let newActive = this.props.name;
		let anchor = "#"+newActive.toLowerCase();
		this.props.setActiveNav(newActive);
		document.querySelector(anchor).scrollIntoView({ 
		  behavior: 'smooth' 
		});
	}

	render() {
		let name = this.props.name;
		let active = this.props.active;
		let anchor = "#"+name.toLowerCase();
		return (
			<React.Fragment>
			<li className={(name === active ? "current" : "")}>
				<a 
					className="smoothscroll"
					href={anchor}
					onClick={this.handleClick}
				>{name}</a>
			</li>
		  </React.Fragment>
		);
	  }
}