import React, { Component } from 'react';
import * as emailjs from "emailjs-com";
export default class Contact extends Component {
	constructor(props) {
		super(props);
		this.state = {
			progress: 0,
			contactName: "",
			contactEmail: "",
			contactSubject: "",
			contactMessage: "",
			error: {
				contactName: false,
				contactEmail: false,
				contactSubject: false,
				contactMessage: false
			}
		};

		// This binding is necessary to make `this` work in the callback
		this.sendForm = this.sendForm.bind(this);
		this.handleNameKeyUp = this.keyUpHandler.bind(this);
        this.handleEmailKeyUp = this.keyUpHandler.bind(this);
		this.handleSubjectKeyUp = this.keyUpHandler.bind(this);
        this.handleMessageKeyUp = this.keyUpHandler.bind(this);
	}
			
	keyUpHandler(e) {
		e.preventDefault();
		const target = e.target;
		const value = target.value;
		const name = target.name;
        console.log(name, value);
        this.setState({
		  [name]: value
		});
		switch(name){
			case "contactName":
			case "contactMessage":
				target.className = "";
			break;
				
			case "contactEmail":
				//validate the entered address
				let pattern = /^[a-zA-Z0-9.!#$%&'*+/=?^_`{|}~-]+@[a-zA-Z0-9](?:[a-zA-Z0-9-]{0,61}[a-zA-Z0-9])?(?:\.[a-zA-Z0-9](?:[a-zA-Z0-9-]{0,61}[a-zA-Z0-9])?)*$/;
		
				if(pattern.test(value)){
					target.className = "";
				}
			break;
		}
    }
	sendForm(e) {
		/*
			STATES FOR SENDING A CONTACT
			0 - not started yet
			1 - validating the form
			2 - validation successful, sending form
			3 - sent successfully
			4 - sending failed
			5 - form invalid
		*/
		e.preventDefault();
		this.setState({
		  progress: 1
		});
		
		//validate the required fields
		let pattern = /^[a-zA-Z0-9.!#$%&'*+/=?^_`{|}~-]+@[a-zA-Z0-9](?:[a-zA-Z0-9-]{0,61}[a-zA-Z0-9])?(?:\.[a-zA-Z0-9](?:[a-zA-Z0-9-]{0,61}[a-zA-Z0-9])?)*$/;
		
		if(
			this.state.contactName.length === 0
			|| this.state.contactEmail.length === 0
			|| !pattern.test(this.state.contactEmail)
			|| this.state.contactMessage.length === 0
		){
			let error = {};
			if(this.state.contactName.length === 0){
				error.contactName = true;
			}
			if(
				this.state.contactEmail.length === 0
				|| !pattern.test(this.state.contactEmail)
			){
				error.contactEmail = true;
			}
			if(this.state.contactMessage.length === 0){
				error.contactMessage = true;
			}
			
			this.setState({
				progress: 5,
				error: error
			});
		}else{
			this.setState({
			  progress: 2
			});
			
			var template_params = {
			   "reply_to": this.state.contactEmail,
			   "from_name": this.state.contactName,
			   "to_name": "From Website",
		   		"subject": this.state.contactSubject,
			   "message_html": this.state.contactMessage
			}
			
			var service_id = "default_service";
			var template_id = "template_j4HMh8wI";
			var user_id = "user_AZUD5aXienxVXMaOfeLQr";
			let that = this;

			emailjs.send(service_id, template_id, template_params, user_id)
	   		.then(function(){ 
			   that.setState({
				  progress: 3
				});
				setTimeout(function(){
					//clear the states - inut values are bound to them
					that.setState({
					  progress: 0,
					  contactName: "",
					  contactEmail: "",
					  contactSubject: "",
					  contactMessage: ""
					});
				}, 5000);
			 }, function(err) {
			   	that.setState({
				  progress: 4
				});
				setTimeout(function(){
					that.setState({
					  progress: 0
					});
				}, 5000);
			});
		}
      
		return false;

	  }
	
	  render() {
		return (
			<React.Fragment>
			<section id="contact" className={
				{
				  0: "clear",
				  1: "validating",
				  2: "sending",
				  3: "sent",
				  4: "failed",
				  5: "invalid"
				}[this.state.progress]
			  }>
				<p className={"notification sending _info"+(this.state.progress === 2 ? " in" : "")}>
					Sending email, please wait...
				</p>
				<p className={"notification sent success"+(this.state.progress === 3 ? " in" : "")}>
					Message sent successfully!
				</p>
				<p className={"notification failed danger"+(this.state.progress === 4 ? " in" : "")}>
					Sending the message failed.
				</p>
				<p className={"notification invalid danger"+(this.state.progress === 5 ? " in" : "")}>
					You have invalid fields.
				</p>
				<div className="row section-head">
				  <div className="two columns header-col">
					<h1><span>Get In Touch.</span></h1>
				  </div>
				  <div className="ten columns">
					<p className="lead">If you are interested in collaborating on a new project, or would like to discuss how I might assist you in improving your existing product by analysing your user's behaviour, do not hesitate to get in touch - I am always interested in potential projects I can get my teeth into.</p>
				  </div>
				</div>
				<div className="row">
				  <div className="eight columns">
					{/* form */}
					<form method="post" id="contactForm" name="contactForm">
					  <fieldset>
						<div>
						  <label htmlFor="contactName">Name <span className="required">*</span></label>
						  <input value={this.state.contactName} type="text" size={35} id="contactName" name="contactName" onChange={this.handleNameKeyUp} className={(this.state.error.contactName ? "danger" : "")}/>
						</div>
						<div>
						  <label htmlFor="contactEmail">Email <span className="required">*</span></label>
						  <input type="text" size={35} id="contactEmail" name="contactEmail" onChange={this.handleEmailKeyUp} className={(this.state.error.contactEmail ? "danger" : "")}/>
						</div>
						<div>
						  <label htmlFor="contactSubject">Subject</label>
						  <input type="text" size={35} id="contactSubject" name="contactSubject" onChange={this.handleSubjectKeyUp} className={(this.state.error.contactSubject ? "danger" : "")}/>
						</div>
						<div>
						  <label htmlFor="contactMessage">Message <span className="required">*</span></label>
						  <textarea cols={50} rows={7} id="contactMessage" name="contactMessage" onChange={this.handleMessageKeyUp} className={(this.state.error.contactMessage ? "danger" : "")}/>
						</div>
						<div>
						  <button className="submit" onClick={this.sendForm}>Submit</button>
						  <span id="image-loader">
							<img alt="Loading Image" src="images/loader.gif" />
						  </span>
						</div>
					  </fieldset>
					</form> {/* Form End */}
					{/* contact-warning */}
					<div id="message-warning"> Error boy</div>
					{/* contact-success */}
					<div id="message-success">
					  <i className="fa fa-check" />Your message was sent, thank you!<br />
					</div>
				  </div>
				  <aside className="four columns footer-widgets">
					<div className="widget widget_contact">
					  <h4>Address and Phone</h4>
					  <p className="address">
						Jamie McDonnell<br />
						866 Emy Destinnove <br />
						Velke Prilepy 25264, Praha Zapad, CZ<br />
						<span>(+420) 777 608 442</span>
					  </p>
					</div>
				  </aside>
				</div>
			  </section> {/* Contact Section End*/}
		  </React.Fragment>
		);
	  }
}